// Converted Variables
// Custom Media Query Variables
/*

  HEIGHTS
  Docs: http://tachyons.io/docs/layout/heights/

  Base:
    h = height
    min-h = min-height
    min-vh = min-height vertical screen height
    vh = vertical screen height

  Modifiers
    1 = 1st step in height scale
    2 = 2nd step in height scale
    3 = 3rd step in height scale
    4 = 4th step in height scale
    5 = 5th step in height scale

    -25   = literal value 25%
    -50   = literal value 50%
    -75   = literal value 75%
    -100  = literal value 100%

    -auto = string value of auto
    -inherit = string value of inherit

  Media Query Extensions:
    -ns = not-small
    -m  = medium
    -l  = large

*/

/* Height Scale */

@include gen {

  @each $key, $val in cfg(height) {
    .h#{$key}#{$sfx} {
      height: $val;
    }
  }

  /* Height Percentages - Based off of height of parent */
  .h-25#{$sfx} {
    height: 25%;
  }
  .h-50#{$sfx} {
    height: 50%;
  }
  .h-75#{$sfx} {
    height: 75%;
  }
  .h-100#{$sfx} {
    height: 100%;
  }
  .min-h-100#{$sfx} {
    min-height: 100%;
  }
  /* Screen Height Percentage */
  .vh-25#{$sfx} {
    height: 25vh;
  }
  .vh-50#{$sfx} {
    height: 50vh;
  }
  .vh-75#{$sfx} {
    height: 75vh;
  }
  .vh-100#{$sfx} {
    height: 100vh;
  }
  .min-vh-100#{$sfx} {
    min-height: 100vh;
  }
  /* String Properties */
  .h-auto#{$sfx} {
    height: auto;
  }
  .h-inherit#{$sfx} {
    height: inherit;
  }
}