// Converted Variables
// Custom Media Query Variables
/*

  UTILITIES

  Media Query Extensions:
    -ns = not-small
    -m  = medium
    -l  = large

*/

/* Equivalent to .overflow-y-scroll */

.overflow-container {
  overflow-y: scroll;
}

@include gen {
  .center#{$sfx} {
    margin-right: auto;
    margin-left: auto;
  }
  .mr-auto#{$sfx} {
    margin-right: auto;
  }
  .ml-auto#{$sfx} {
    margin-left: auto;
  }
}